import React, {Component} from "react"
import MyNavLink from "../../Components/MyNavLink";
import {Menu, Button} from 'antd';
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
} from '@ant-design/icons';

const data = [
    ["stack-queue-list", "栈、队列与链表"],
    ["sort", "各种排序算法"],
    ["heap", "堆"],
    ["union-find-disjoint-sets", "并查集"],
    ["bst", "二叉搜索树"],
]

export default class DSALeftBar extends Component {
    state = {
        collapsed: false,
    };

    toggleCollapsed = () => {
        this.setState({
            collapsed: !this.state.collapsed,
        });
    };

    render() {
        return (
            <div style={{width: 256}}>
                <Button type="primary" onClick={this.toggleCollapsed} style={{marginBottom: 16}}>
                    {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined)}
                </Button>
                <Menu inlineCollapsed={this.state.collapsed}>
                    {
                        data.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/data-structure&algorithm/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                </Menu>
            </div>
        );
    }
}